let k = 0;
let c = 0;
let α = 0;
let l = 0;
let T = 0;
let R = 0;
let β = 0;
let n = 0;

const setParameters = (params) => {
  k = params.k;
  c = params.c;
  α = params.α;
  l = params.l;
  T = params.T;
  R = params.R;
  β = params.β;
  n = params.n;
};

const countX = 21;
const countY = 6;

let firstGraph = [];
let secondGraph = [];

export const getFirstGraphArray = () => {
  return firstGraph;
};

export const getSecondGraphArray = () => {
  return secondGraph;
};

export const startCalculate = (params) => {
  setParameters(params);
  firstGraph = [];
  secondGraph = [];

  let r = 0;
  while (r <= R + 0.001) {
    let t = 0;
    let numberOfT = 0;
    const objectForPush = {
      r: r.toFixed(2),
    };
    while (t <= T) {
      objectForPush[`t${numberOfT} = ${t.toFixed(2)}`] = U(r, t);
      t += T / countY;
      numberOfT++;
    }
    firstGraph.push(objectForPush);
    r += R / (countX - 1);
  }

  let t = 0;
  while (t <= T + 0.001) {
    let r = 0;
    let numberOfR = 0;
    const objectForPush = {
      t: t.toFixed(2),
    };
    while (r <= R) {
      objectForPush[`r${numberOfR} = ${r.toFixed(2)}`] = U(r, t);
      r += R / countY;
      numberOfR++;
    }
    secondGraph.push(objectForPush);
    t += T / (countX - 1);
  }
};

const J_n = (x, N) => {
  let result = Math.pow(x / 2, N);
  for (let k = 0; k < n; k++) {
    result +=
      (Math.pow(-1, k) * Math.pow(x / 2, 2 * k)) /
      factorial(k) /
      factorial(N + k);
  }
  return result;
};

const λ = (i) => {
  return (-Math.pow(μ[i], 2) * k) / Math.pow(R, 2) - (2 * α) / l;
};

const B = (i) => {
  return (600 * β * J_n(μ[i], 1)) / (Math.pow(J_n(μ[i], 0), 2) * μ[i]);
};

const U = (r, t) => {
  let result = 0;
  for (let i = 1; i <= n; i++) {
    result +=
      ((B(i) * (Math.pow(Math.E, (λ(i) * t) / c) - 1)) / λ(i)) *
      J_n((μ[i] * r) / R, 0);
  }
  return result;
};

const factorial = (x) => {
  let result = 1;
  if (x === 0) {
    return 1;
  }
  if (x < 0) {
    console.log("Беда факториал");
    return undefined;
  }
  for (let i = 2; i <= x; i++) {
    result *= i;
  }
  return result;
};
